
.details {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.details img {
    width: 10rem;
}
.details p {
    margin: .3rem 0;
    text-align: left;
    text-indent: 2em;
}
.details .describe {
    position: relative;
}
.details .describe p {
      text-align: center;
}
.details .describe div {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 1rem 0;
      background-color: rgba(255, 255, 255, 0.6);
}
.details h3 {
    margin-bottom: .5rem;
}



