
.list {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.menuList li {
  position: relative;
  margin: 0;
  float: none;
  border-bottom: .5rem solid #f0f0f0;
}
.menuList li:last-child {
  border-bottom: none;
}
.menuList img {
  width: 10rem;
  vertical-align: bottom;
}
.menuList div {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 1rem 0;
  background-color: rgba(255, 255, 255, 0.6);
}
.menuList h3 {
  margin-bottom: .5rem;
}



